/*GENERAL*/

body {
  font-family: "Mazius-Italic";
}

#Home {
  overflow: hidden;
}

.section {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100dvh;
  z-index: 1;
}

#canvasContainer {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100dvh;
}

#myVideo {
  position: fixed;
  right: 0;
  bottom: 0;
  min-width: 100%;
  min-height: 100%;
  filter: contrast(100%);
}

/*NAVBAR*/

#navBarHome {
  position: absolute;
  top: 10px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 90%;
  height: 100px;
}

.linkNav {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  width: 20vw;
  height: 40px;
  font-family: 'Mazius';
}

.linkNav:hover {
  text-decoration: underline;
}

#navBarHome a {
  color: black;
  text-decoration: none;
  font-size: 20px;
  z-index: 2
}

#homeLogo {
  width: 300px;
  height: 150px;
  background-size: 100% 100%;
}

/*TALENT*/

#talentTitle {
  position: absolute;
  left: 50%;
  top: 30%;
  transform: translateX(-50%);
  color: black;
  transition: 1s cubic-bezier(.07, .77, .44, .99);
  font-size: 25px;
  font-family: "Mazius";

}

#talent {
  position: absolute;
  left: 50%;
  top: 45%;
  transform: translate(-50%, -50%);
  width: 75%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  transition: 1s cubic-bezier(.05, .7, .3, 1);
}

#talent h1 {
  cursor: pointer;
  font-weight: normal;
  flex: 0 0 15%;
  text-align: center;
  font-size: 30px;
}

#talent h1:hover {
  text-decoration: underline;
}

.divider {
  width: 2%;
  color: red;
  font-weight: bold;
}

/*BRANDS*/

#brands {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 200px;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  background-color: black;
}

#brandsTop {
  display: flex;
  width: 100%;
  height: 50%;
}

#brandsBottom {
  display: flex;
  width: 100%;
  height: 50%;
}

.ticker-track-top {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: unset;
  flex-grow: 0;
  flex-shrink: 0;
}

.brandsAnimationTop {
  animation: ticker 30s linear infinite;
  -webkit-animation: ticker 30s linear infinite;
}

.ticker-track-bottom {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: unset;
  flex-grow: 0;
  flex-shrink: 0;
}

.brandsAnimationBottom {
  animation: ticker 30s linear infinite reverse;
  -webkit-animation: ticker 30s linear infinite reverse;
}

.ticker-img {
  width: auto;
  height: 50%;
  padding: 0 50px;
  filter: invert(1) brightness(2);
}

@keyframes ticker {
  0% {
    transform: translateX(0%);
  }

  100% {
    transform: translateX(-100%);
  }
}

@-webkit-keyframes ticker {
  0% {
    transform: translateX(0%);
  }

  100% {
    transform: translateX(-100%);
  }
}

/*SPOTIFY RADIO*/

#spotifyContainer {
  position: absolute;
  right: 0;
  bottom: -385px;
  width: 20%;
  background-color: white;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  transition: .75s;
}

#spotifyOpen {
  display: flex;
  width: 100%;
  height: 100px;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  font-family: 'Mazius'
}

#spotifyOpen h1 {
  font-size: 1.3vw;
}

#spotifyArrow {
  width: auto;
  height: 1.3vw;
  margin-right: 10px;
  filter: grayscale(100%) brightness(0)
}

#spotifyIntegration {
  background-color: #535353;
}

/*ABOUT*/

#aboutText {
  z-index: 2;
  position: absolute;
  text-align: justify;
  text-align-last: center;
  left: 50%;
  top: 42%;
  transform: translate(-50%, -50%);
  color: black;
  width: 40%;
  font-size: 22px;
}

#contactInfo {
  z-index: 2;
  position: absolute;
  top: 57%;
  left: 50%;
  width: 40%;
  transform: translateX(-50%);
  display: flex;
  justify-content: space-evenly;
}

.contacts {
  width: 5vw;
  height: 5vh;
  color: black;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Mazius-Bold";
}

.aboutIcon {
  height: 100%;
  width: auto;
  cursor: pointer;
  opacity: 1;
  filter: grayscale(100%) brightness(0);
}

.aboutIcon:hover {
  opacity: .5;
}

.credits {
  z-index: 2;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  position: absolute;
  color: black;
  font-size: 12px;
}

.credits a {
  color: black;
  font-family: "Mazius-Bold";
  text-decoration: none;
}

.credits a:hover {
  text-decoration: underline;
}

/*GALLERY*/

#cursor {
  position: absolute;
  transform: translate(-50%, -50%);
  color: white;
  font-family: "Mazius-ItalicBold";
  font-size: 20px;
}

/*TALENTPAGE*/

#talentName {
  text-align: center;
  font-family: 'Mazius-Bold';
}

#talentInfo {
  position: absolute;
  top: 20%;
  left: 50%;
  transform: translateX(-50%);
  width: 90%;
}

#talentImages {
  height: 75%;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.talentImage {
  height: 25vw;
  width: auto;
  margin: 0;
  margin: 20px 0;
}

#talentContacts {
  text-align: center;
}

#talentContacts span {
  color: red;
}

.talentContact {
  color: black;
  cursor: pointer;
  text-decoration: none;
  font-size: 20px;
  margin: 0 10px;
}

.talentContact:hover {
  text-decoration: underline;
}

/*BUTTONS*/

#fontChanger {
  position: absolute;
  left: 10px;
  cursor: pointer;
  color: white;
}

#fontChanger p {
  display: inline-block;
  margin: 0px 10px;
  mix-blend-mode: difference;
}

#fontChanger p:hover {
  text-decoration: underline;
}

.slidecontainer {
  color: white;
  position: absolute;
  right: 10px;
  width: 150px;
}

.slidecontainer p {
  mix-blend-mode: difference;
}

#removeBackground {
  color: white;
  position: absolute;
  right: 60px;
  text-align: center;
  cursor: pointer;
  mix-blend-mode: difference;
}

#removeBackground:hover {
  text-decoration: underline;
}